// Import any necessary dependencies
import {
    UPLOAD_FILES,
    UPLOAD_FILES_SUCCESS,
    UPLOAD_FILES_FAILURE,
    FETCH_FILE_DETAILS,
    FETCH_FILE_DETAILS_SUCCESS,
    FETCH_FILE_DETAILS_FAILURE,
    DELETE_FILE,
    DELETE_FILE_SUCCESS,
    DELETE_FILE_FAILURE,
    FETCH_TOTAL_RECORDS_SUCCESS,
    UPDATE_ANY_STATE
} from './actions';

// Initial state
const initialState = {
    loading: false,
    error: null,
    fileDetails: null,
    totalRecords: 0,
    operationDate: new Date().toISOString().split('T')[0],
    updateViewFIle: null,
    htmlContent: null,
    errorViewFile: '',
    page: 0,
    rowsPerPage: 10,
    searchQuery: '',
    searchOption: '',
    error_files: []
};

// Reducer function
const uploadReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_ANY_STATE:
            return Object.assign({}, state, action.payload);
        case UPLOAD_FILES:
            return {
                ...state,
                loading: true,
                error: null
            };
        case UPLOAD_FILES_SUCCESS:
            return {
                ...state,
                loading: false,
                error: null,
                error_files: action.payload.files
            };
        case UPLOAD_FILES_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload
            };
        case FETCH_FILE_DETAILS:
            return {
                ...state,
                loading: true,
                error: null
            };
        case FETCH_FILE_DETAILS_SUCCESS:
            return {
                ...state,
                loading: false,
                error: null,
                fileDetails: action.payload.data
                // totalRecords: action.payload.totalRecords
            };
        case FETCH_FILE_DETAILS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload
            };
        case FETCH_TOTAL_RECORDS_SUCCESS:
            return {
                ...state,
                totalRecords: action.payload // Update the totalRecords from the action payload
            };
        case DELETE_FILE:
            return {
                ...state,
                loading: true,
                error: null
            };
        case DELETE_FILE_SUCCESS:
            return {
                ...state,
                loading: false,
                error: null
            };
        case DELETE_FILE_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload
            };
        case 'UPDATE_FILE_DATE':
            return {
                ...state,
                operationDate: action.payload.operationDate
                // Add any other necessary state updates
            };
        case 'UPDATE_VIEW_FILE':
            return {
                ...state,
                loading: true,
                updateViewFIle: action.payload.updateViewFIle
                // Add any other necessary state updates
            };
        case 'FETCH_HTML_CONTENT_SUCCESS':
            return {
                ...state,
                loading: false,
                htmlContent: action.payload
            };
        case 'FETCH_HTML_CONTENT_FAILURE':
            return {
                ...state,
                errorViewFile: action.payload
            };
        default:
            return state;
    }
};

export default uploadReducer;
