import { lazy } from 'react';

// project imports
import MainLayout from 'layout/MainLayout';
import Loadable from 'ui-component/Loadable';

// dashboard routing
const DashboardDefault = Loadable(lazy(() => import('views/dashboard/Default')));
const DailyDocumentUpload = Loadable(lazy(() => import('views/DailyDocumentUpload/DailyDocumentUpload')));
const KeywordsDataEntry = Loadable(lazy(() => import('views/KeywordsDataEntry/KeywordsDataEntry')));
const KeywordBasedAnalysis = Loadable(lazy(() => import('views/KeywordBasedAnalysis/KeywordBasedAnalysis')));
const KeywordSummaryTable = Loadable(lazy(() => import('views/KeywordSummaryTable/KeywordSummaryTable')));
const KeywordGraphView = Loadable(lazy(() => import('views/KeywordGraphView/KeywordGraphView')));

// ==============================|| MAIN ROUTING ||============================== //

const MainRoutes = {
    path: '/',
    element: <MainLayout />,
    children: [
        {
            path: '/',
            element: <DashboardDefault />
        },
        {
            path: '/Daily-Document-Upload',
            element: <DailyDocumentUpload />
        },
        {
            path: '/KeywordsDataEntry',
            element: <KeywordsDataEntry />
        },
        {
            path: '/KeywordBasedAnalysis',
            element: <KeywordBasedAnalysis />
        },
        {
            path: '/KeywordSummaryTable',
            element: <KeywordSummaryTable />
        },
        {
            path: '/KeywordGraphView',
            element: <KeywordGraphView />
        },
        {
            path: 'dashboard',
            children: [
                {
                    path: '',
                    element: <DashboardDefault />
                }
            ]
        }
    ]
};

export default MainRoutes;
