import {
    FETCH_RECORDS_REQUEST,
    FETCH_RECORDS_SUCCESS,
    FETCH_RECORDS_FAILURE,
    ADD_RECORD_SUCCESS,
    DELETE_RECORD_SUCCESS,
    UPDATE_RECORD_SUCCESS,
    FETCH_KEYWORD_ANALYSIS_RECORDS_REQUEST,
    FETCH_KEYWORD_ANALYSIS_RECORDS_REQUEST_SUCCESS,
    FETCH_KEYWORD_ANALYSIS_RECORDS_REQUEST_FAILURE,
    FETCH_KEYWORD_SUMMARY_RECORDS_REQUEST,
    FETCH_KEYWORD_SUMMARY_RECORDS_REQUEST_SUCCESS,
    FETCH_KEYWORD_SUMMARY_RECORDS_REQUEST_FAILURE,
    FETCH_TOTAL_KEYWORD_ENTRY_RECORDS_SUCCESS,
    FETCH_TOTAL_KEYWORD_ENTRY_RECORDS_FAILURE,
    FETCH_TOTAL_KEYWORD_ANALYSIS_RECORDS_SUCCESS,
    FETCH_TOTAL_KEYWORD_ANALYSIS_RECORDS_FAILURE,
    FETCH_TOTAL_KEYWORD_SUMMARY_RECORDS_SUCCESS,
    FETCH_TOTAL_KEYWORD_SUMMARY_RECORDS_FAILURE,
    UPDATE_ANY_KEYWORD_STATE,
    UPDATE_QUALIFIED_STATUS,
    ADD_RECORD_REQUEST,
    DELETE_RECORD_REQUEST,
    UPDATE_RECORD_REQUEST
} from './actions';

const initialState = {
    records: [],
    keywordSummaryRecords: [],
    keywordAnalysisRecords: { daily_upload_data: [], search_keywords_categories: [] },
    loading: false,
    error: null,
    totalKeywordEntryRecords: 0,
    totalKeywordAnalysisRecords: 0,
    totalKeywordSummaryRecords: 0,
    kdepage: 0,
    kderowsPerPage: 10,
    kdedateRange: {
        startDate: new Date().toISOString().split('T')[0],
        endDate: new Date().toISOString().split('T')[0],
        key: 'selection'
    },
    kdesearchQuery: '',
    kdesearchOption: '',

    kbasearchQuery: '',
    kbasearchOption: 'All',
    kbasearchLevel: 'All',
    kbasearchQualified: 'All',
    kbadateRange: {
        startDate: new Date().toISOString().split('T')[0],
        endDate: new Date().toISOString().split('T')[0],
        key: 'selection'
    },
    kbapage: 0,
    kbarowsPerPage: 10,

    kstpage: 0,
    kstrowsPerPage: 10,
    kstsearchQuery: '',
    kstsearchOption: 'All',
    kstdateRange: {
        startDate: new Date().toISOString().split('T')[0],
        endDate: new Date().toISOString().split('T')[0],
        key: 'selection'
    }
};

const keywordReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_ANY_KEYWORD_STATE:
            return Object.assign({}, state, action.payload);
        case FETCH_RECORDS_REQUEST:
            return {
                ...state,
                loading: true,
                error: null
            };
        case ADD_RECORD_REQUEST:
            return {
                ...state,
                loading: true,
                error: null
            };
        case DELETE_RECORD_REQUEST:
            return {
                ...state,
                loading: true,
                error: null
            };
        case UPDATE_RECORD_REQUEST:
            return {
                ...state,
                loading: true,
                error: null
            };
        case UPDATE_QUALIFIED_STATUS:
            return {
                ...state,
                loading: true,
                error: null
            };
        case FETCH_RECORDS_SUCCESS:
            return {
                ...state,
                records: action.payload,
                loading: false,
                error: null
            };
        case FETCH_RECORDS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload
            };
        case FETCH_KEYWORD_ANALYSIS_RECORDS_REQUEST:
            return {
                ...state,
                loading: true,
                error: null
            };
        case FETCH_KEYWORD_ANALYSIS_RECORDS_REQUEST_SUCCESS:
            return {
                ...state,
                keywordAnalysisRecords: action.payload,
                loading: false,
                error: null
            };
        case FETCH_KEYWORD_ANALYSIS_RECORDS_REQUEST_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload
            };
        case FETCH_KEYWORD_SUMMARY_RECORDS_REQUEST:
            return {
                ...state,
                loading: true,
                error: null
            };
        case FETCH_KEYWORD_SUMMARY_RECORDS_REQUEST_SUCCESS:
            return {
                ...state,
                keywordSummaryRecords: action.payload,
                loading: false,
                error: null
            };
        case FETCH_KEYWORD_SUMMARY_RECORDS_REQUEST_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload
            };
        case ADD_RECORD_SUCCESS:
            return {
                ...state,
                records: [...state.records, action.payload]
            };
        case DELETE_RECORD_SUCCESS:
            return {
                ...state,
                records: state.records.filter((record) => record.id !== action.payload)
            };
        case UPDATE_RECORD_SUCCESS:
            return {
                ...state,
                records: state.records.map((record) => (record.id === action.payload.id ? action.payload : record))
            };
        case FETCH_TOTAL_KEYWORD_ENTRY_RECORDS_SUCCESS:
            return {
                ...state,
                totalKeywordEntryRecords: action.payload // Update the totalRecords from the action payload
            };
        case FETCH_TOTAL_KEYWORD_ENTRY_RECORDS_FAILURE:
            return {
                ...state,
                error: action.payload
            };
        case FETCH_TOTAL_KEYWORD_ANALYSIS_RECORDS_SUCCESS:
            return {
                ...state,
                totalKeywordAnalysisRecords: action.payload // Update the totalRecords from the action payload
            };
        case FETCH_TOTAL_KEYWORD_ANALYSIS_RECORDS_FAILURE:
            return {
                ...state,
                error: action.payload
            };
        case FETCH_TOTAL_KEYWORD_SUMMARY_RECORDS_SUCCESS:
            return {
                ...state,
                totalKeywordSummaryRecords: action.payload // Update the totalRecords from the action payload
            };
        case FETCH_TOTAL_KEYWORD_SUMMARY_RECORDS_FAILURE:
            return {
                ...state,
                error: action.payload
            };
        default:
            return state;
    }
};

export default keywordReducer;
