// Action creators
export const uploadFiles = (operationDate, category, files) => ({
    type: UPLOAD_FILES,
    payload: { operationDate, category, files }
});

export const uploadFilesSuccess = (files) => ({
    type: UPLOAD_FILES_SUCCESS,
    payload: { files }
});

export const uploadFilesFailure = (error) => ({
    type: UPLOAD_FILES_FAILURE,
    payload: error
});

export const fetchFileDetails = (date, page, rowsPerPage, searchQuery = '', searchOption = '') => ({
    type: FETCH_FILE_DETAILS,
    payload: { date, page, rowsPerPage, searchQuery, searchOption }
});

export const fetchFileDetailsSuccess = (data) => ({
    type: FETCH_FILE_DETAILS_SUCCESS,
    payload: { data }
});
export const fetchFileDetailsFailure = (error) => ({
    type: FETCH_FILE_DETAILS_FAILURE,
    payload: error
});

