// Saga worker function for fetching records
function* fetchRecordsWorker(action) {
    try {
        const page = yield select((state) => state.keyword.kdepage);
        const rowsPerPage = yield select((state) => state.keyword.kderowsPerPage);
        const searchQuery = yield select((state) => state.keyword.kdesearchQuery);
        const searchOption = yield select((state) => state.keyword.kdesearchOption);
        const startDate = yield select((state) => state.keyword.kdedateRange.startDate);
        const endDate = yield select((state) => state.keyword.kdedateRange.endDate);
        const records = yield call(fetchRecordsAPI, page, rowsPerPage, searchQuery, searchOption, startDate, endDate);
        yield put(fetchRecordsSuccess(records));
    } catch (error) {
        yield put(fetchRecordsFailure(error.message));
    }
}

// Saga worker function for adding a record
function* addRecordWorker(action) {
    try {
        // const record = yield call(addRecordAPI, action.payload);
        yield call(addRecordAPI, action.payload);

        const page = yield select((state) => state.keyword.kdepage);
        const rowsPerPage = yield select((state) => state.keyword.kderowsPerPage);
        const searchQuery = yield select((state) => state.keyword.kdesearchQuery);
        const searchOption = yield select((state) => state.keyword.kdesearchOption);
        const startDate = yield select((state) => state.keyword.kdedateRange.startDate);
        const endDate = yield select((state) => state.keyword.kdedateRange.endDate);
        const totalRecords = yield call(fetchTotalKeywordEntryRecordsAPI, searchQuery, searchOption, startDate, endDate);
        yield put(fetchTotalKeywordEntryRecordsSuccess(totalRecords));

        const records = yield call(fetchRecordsAPI, page, rowsPerPage, searchQuery, searchOption, startDate, endDate);
        yield put(fetchRecordsSuccess(records));
        // yield put(addRecordSuccess(record));
    } catch (error) {
        yield put(addRecordFailure(error.message));
    }
}

