function* handleUpdateQualifiedStatusWorker(action) {
    try {
        const { recordId, status } = action.payload;

        yield call(updateQualifiedStatusAPI, recordId, status);

        yield put(fetchKeywordAnalysisRecords());
        yield put(fetchTotalKeywordAnalysisRecords());
    } catch (error) {
        yield put(fetchTotalRecordsFailure(error.message));
    }
}

function* fetchTotalRecordsWatcher() {
    yield takeLatest(FETCH_TOTAL_RECORDS, fetchTotalRecordsWorker);
}
function* fetchTotalKeywordEntryRecordsWatcher() {
    yield takeLatest(FETCH_TOTAL_KEYWORD_ENTRY_RECORDS, fetchTotalKeywordEntryRecordsWorker);
}
function* fetchTotalKeywordAnalysisRecordsWatcher() {
    yield takeLatest(FETCH_TOTAL_KEYWORD_ANALYSIS_RECORDS, fetchTotalKeywordAnalysisRecordsWorker);
}
function* fetchTotalKeywordSummaryRecordsWatcher() {
    yield takeLatest(FETCH_TOTAL_KEYWORD_SUMMARY_RECORDS, fetchTotalKeywordSummaryRecordsWorker);
}
function* watchUpdateQualifiedStatus() {
    yield takeLatest(UPDATE_QUALIFIED_STATUS, handleUpdateQualifiedStatusWorker);
}
// Root Saga
export default function* rootSaga() {
    yield all([
        uploadFilesWatcher(),
        fetchFileDetailsWatcher(),
        fetchTotalRecordsWatcher(),
        fetchTotalKeywordEntryRecordsWatcher(),
        fetchTotalKeywordAnalysisRecordsWatcher(),
        deleteFileWatcher(),
        watchUpdateViewFile(),
        recordsWatcher(),
        fetchKeywordAnalysisRecordsWatcher(),
        fetchTotalKeywordSummaryRecordsWatcher(),
        fetchKeywordSummaryRecordsWatcher(),
        watchUpdateQualifiedStatus()
        // Other sagas...
    ]);
}
